body{
  text-align: center;
  background: #000;
  color:#fff;
  width:100%;
}

header nav{
  /* background:#777; */
  display:flex;
  justify-content:space-around;
  align-items:center;
  width:45vw;
  height:50px;
  padding:10px 0;
}
header nav a{
  color:#000;
  text-decoration:none;
  background:#fff;
  border-radius:25%;
  padding:10px;
  margin-left:10px;
  font-size:1vw;
}
h3{display: inline-block;}
.instruction{
  display: block;
  margin:20px auto;
  color:#fff;
}
table{
  margin:20px auto;
  font-size:1.4vw;
  border-collapse: collapse;
  text-align: left;
  font-size:0.9vw;
}
table thead tr th{
  border-bottom:1px solid #fff;
}
.movieListTable thead tr th:nth-of-type(1){width:5vw;}

#root .contents .graphContainer div div svg text{fill:#fff !important;}

@media(max-width:1024px){
  header nav a{font-size:2vw;}
  table{font-size:1.5vw;}
  .movieListTable thead tr th:nth-of-type(1){width:9vw;}
}

@media(max-width:540px) {
  header nav a{padding:5px;}
  table{font-size:1.5vw;}
  table thead tr th:first-child{width:20vw;}
  .movieListTable thead tr th:nth-of-type(1){width:12vw;}
  .movieListTable thead tr th:nth-of-type(3){width:15vw;}
}
